import React, {useState, useEffect} from 'react'
import {useSelector, useDispatch} from 'react-redux'
import currencyFormatter from 'currency-formatter'
import {BsDash, BsPlus} from 'react-icons/bs'
import { IoMdClose } from "react-icons/io";
import './Cart.css'

const Cart = () => {
    const {totalCartProducts} = useSelector(state => state.CartReducers)   
    const {totalProducts, totalPrice, totalQuantity} = useSelector(state => state.ProductReducers) 
    const dispatch = useDispatch()  
    const [isCartShow, setCartShow] = useState("closed")    
    
    useEffect(()=>{
        setCartShow('opened')         
    }, [totalProducts])     

    const onHandlerCheckout = () => {        
        alert("Checkout - Subtotal: " + "$" + totalPrice)
    }
   
    return (
        <div className = {`cart-container ${isCartShow}`} >
            <button className = "close-btn" onClick = {() =>{
                setCartShow("closed")                 
            }}><IoMdClose/></button>
            <div className = "cart" onClick = {() =>{
                setCartShow("opened")
             }}>
                <span className = "logo"></span>
                <span className = "count">{totalQuantity}</span>
                <span className = "title">Cart</span>
            </div> 
            <div className = "cart-product">
                 {totalCartProducts.length > 0 ?
                     totalCartProducts.map((product, index) => ( 
                    <div className = "cart-details" key = {product.id}>    
                        <div className = "image">
                            <img src = {product.cartImg}  alt={product.name}/>                        
                        </div>
                        <div className = "cart-info">
                            <div className  = "name">{product.name}</div>
                            <div className  = "size">
                                <span>{product.size} {product.info}</span>
                            </div>
                            <div className = "quantity">
                                Quantity {product.qty}
                            </div>    
                        </div>
                        <div className = "cart-price-details">
                            <div className =  "delete-product "   
                                onClick = {() => dispatch({type: 'REMOVE_PRODUCT', id:product.id, product})
                                }>
                                <span className = "icon"></span>
                            </div>
                            <div className = "actualPrice">{currencyFormatter.format(product.price * product.qty, {code:'USD'})}</div>
                            <span className={"decrement symbol " + (product.qty === 1 ? 'disabled' : '') } onClick = {() => dispatch({type: 'DECREMENT', id:product.id, product})}> <BsDash/></span>
                            <span className="increment symbol" onClick = {() => dispatch({type: 'INCREMENT', id:product.id, product})}> <BsPlus/></span>
                        </div>
                    </div>  
                )) : <p className = "massage">Add some products in the cart !</p> } 
            </div> 
            <div className = "cart-sumamry">
                <div className = "info">
                    <p className = "text">SUBTOTAL</p>
                    <p className = "total">{currencyFormatter.format(totalPrice, {code:'USD'})}</p>                    
                </div>  
                <button type="button" disabled = {totalPrice === 0} className={"checkout "+ (totalPrice === 0 ? 'disabled' : '')}
                    onClick = {onHandlerCheckout}>Checkout</button>  
            </div>   
        </div>
    )
}

export default Cart
