import './App.css';
import {BrowserRouter as Router} from 'react-router-dom'
import TopBar from './components/TopBar'
import Home from './components/Home'
import{Provider} from 'react-redux'
import store from './store'

function App() {
  return (
    <Router>
      <Provider store = {store}>
        <div className="App">
            <TopBar/>
            <Home/>             
        </div>
      </Provider>
    </Router>
  );
}

export default App;
