import React from 'react'
import currencyFormatter from 'currency-formatter'

const Products = ({props}) => {
    return (
        <div className = "product-details">
            <div className = "image">
                <img src = {props.prodImg}  alt={props.name} />                        
            </div>
            <div className = "status">{props.status ? <span>{props.status}</span> : ''}</div>
            <div className = "name"> 
                {props.name}
            </div> 
            <div className="line"></div>
            <div className = "product-price">
                <span className = "actualPrice">{currencyFormatter.format(props.price, {code:'USD'})}</span>                                    
            </div>
            <div className = "product-price">
                <span className = "discount">or {props.discount} x</span>
                <span className = "disSountcurrency">{currencyFormatter.format(props.discountPrice, {code:'USD'})}</span>                                    
            </div>
            <div className = "add-to-cart" >Add to cart </div>
        </div>
    )
}

export default Products
