const initState = {    
    totalCartProducts: [] 
}

const CartReducers  = (state = initState, action) =>{   
    const {totalCartProducts} = state      
    let product;    
    switch(action.type){
        case 'ADD_TO_CART':
            const check = totalCartProducts.find(product => product.id === action.id)
            if(check){
                return state
            }else{
                product = action.product;                 
                return {
                    ...state, 
                    totalCartProducts: [...state.totalCartProducts, product] 
                }
            }
        break;           
        case 'REMOVE_PRODUCT':         
            const filtered = totalCartProducts.filter(product => product.id !== action.id);
            return {
                ...state,
                totalCartProducts: filtered 
            } 
           break;            
        default:
            return state;
    }
}
 
export default CartReducers