import React, {useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import Products from './Products'
import Cart from './Cart'
import './Home.css'
 
const Home = () => {
    const {products, totalProducts} = useSelector(state => state.ProductReducers) 
    const [sortType, setSortType] = useState("")
    const [sortSizeType, setSortSizeType] = useState("")
    const dispatch = useDispatch()
     
    const sortProducts = (event) =>{
        const sort = event.target.value         
        setSortType(sort)        
        return products.sort((a,b) => 
            sort === "highest"
            ? a.price < b.price 
            ? 1 : -1  
            : sort === "lowest"   
            ? a.price > b.price
            ? 1 : - 1
            : a.id > b.id ? 1 : -1
        )      
    }

    const filterProducts = (event) => {             
        setSortSizeType(event.target.value)        
    }
    
    return (
        <div className = "home">
            <div className = "item-container">
                <div className = "item-left">
                    <div className = "container">
                        <div className = "text">Sizes:</div>
                        <div className = "size-buttons">                            
                            <div className="filter-size">  
                                <select className = "select" onChange = {filterProducts}>
                                    <option value="All">Select</option>
                                    <option value="XS">XS</option>
                                    <option value="S">S</option>
                                    <option value="M">M</option>
                                    <option value="ML">ML</option>
                                    <option value="L">L</option>
                                    <option value="XL">XL</option>
                                    <option value="XXL">XXL</option>
                                </select>
                            </div>
                        </div>
                    </div> 
                </div>    
                <div className = "item-right">
                    <div className = "total-Product">
                        <p>{products.length} Product(s) found.</p>                        
                        <div className="filter-sort">  
                            <p className = "text">Order By</p>
                            <select className = "select" onChange = {sortProducts}>
                                <option value="latest">Select</option>
                                <option value="lowest">Lowest to Highest</option>
                                <option value="highest">Highest to Lowest</option>
                            </select>
                        </div>     
                    </div>                
                    <div className = "row">
                        {products.filter((val)=> 
                            sortSizeType === "All" ? val : val.size.toString().toLowerCase().includes(sortSizeType.toString().toLowerCase())
                        ).map(product =>(
                            <button className = "product" key = {product.id}
                             onClick = {() => dispatch({type:'ADD_TO_CART', id:product.id, product})}>
                                <Products props = {product}/>
                            </button>   
                        ))}
                    </div>
                </div>
            </div>
            {totalProducts.length >= 0 && <Cart/>}  
        </div>       
    )
}

export default Home